.c-listed-lines {
  width: 1190px;
  height: 525px;
  padding-left: 10px;
}

.c-listed-lines > div {
  float: left;
  width: 32.33%;
  position: relative;
  border-right: 2px solid #eee;
  box-sizing: border-box;
  padding: 6px;
  height: auto;
}

.c-listed-lines div.title {
  line-height: 54px;
  font-size: 28px;
  text-transform: capitalize;
  border-top-color: transparent;
  border-bottom-color: transparent !important;
}

.c-listed-lines > div > div + div {
  border-top: 2px solid #eee;
}

.c-listed-lines > div > div:last-child {
  border-bottom: 2px solid #eee;
}

.c-listed-lines > div > div.title + div {
  border-top-color: transparent;
}

.c-listed-lines > div:last-child {
  float: left;
  width: 32.33%;
  position: relative;
  border-right: none;
  box-sizing: border-box;
  padding: 6px;
  height: auto;
}

.c-listed-lines > div:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

.c-listed-lines > div > * {
  clear: both;
  padding: 2.25px 0;
  zborder-bottom: 2px solid transparent;
}

.c-listed-lines > div > *:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

.c-listed-lines > div > * > * {
  float: left;
}

.c-listed-lines label:first-child {
  width: 33.33%;
  box-sizing: border-box;
  padding-left: 6px;
  font-family: 'Oswald';
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.c-listed-lines .title label:first-child {
  width: 10%;
}

.c-listed-lines .title label:last-child {
  width: 90%;
  border-top: 2px solid #eee;
  margin-top: 27px;
}

.c-listed-lines > div > * > div {
  width: 16%;
  box-sizing: border-box;
  padding: 4px 4px;
  zperspective: 20px;
}

.c-listed-lines > div > * > div span {
  width: 100%;
  height: 12px;
  display: block;
  border-radius: 4px;
  transform: rotateX(90deg);
  transition: .35s cubic-bezier(0, .25, 0.25, 1);
  opacity: .15;
}

.c-listed-lines > div > * > div span.show {
  transform: rotateX(0);
  opacity: 1;
}

