[type="checkbox"]:not(:checked),
[type="checkbox"]:checked {
  position: absolute;
  left: -9999px;
}

[type="checkbox"]:not(:checked) + label,
[type="checkbox"]:checked + label {
  position: relative;
  padding-left: 32px;
  cursor: pointer;
  margin-bottom: 5px;
  display: inline-block;
  font-size: 10.5pt;
}

.membership-form [type="checkbox"]:not(:checked) + label,
.membership-form [type="checkbox"]:checked + label {
  font-size: 10pt;
}

.forms {
  width: 50%;
  float: left;
  padding-right: 15px;
}

.forms-right {
  width: 50%;
  float: left;
}

/* checkbox aspect */

[type="checkbox"]:not(:checked) + label:before,
[type="checkbox"]:checked + label:before {
  content: '';
  position: absolute;
  left: 0px;
  top: 0px;
  width: 22px;
  height: 22px;
  border: 1px solid #ccc;
  background: #ffffff;
  border-radius: 0px;
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, .1);
}

/* checked mark aspect */

[type="checkbox"]:not(:checked) + label:after,
[type="checkbox"]:checked + label:after {
  content: '\2022';
  position: absolute;
  top: -4px;
  left: 5px;
  font-size: 25px;
  line-height: 1.2;
  color: #bb0076;
  transition: all .2s;
}

/* checked mark aspect changes */

[type="checkbox"]:not(:checked) + label:after {
  opacity: 0;
  transform: scale(0);
}

[type="checkbox"]:checked + label:after {
  opacity: 1;
  transform: scale(1);
}

/* accessibility */

[type="checkbox"]:checked:focus + label:before,
[type="checkbox"]:not(:checked):focus + label:before {
  border: 2px solid #F566C0;
}

/* hover style just for information */

label:hover:before {
  border: 2px solid #F566C0 !important;
  background: #FFFFFF;
}

