.tax {
  position: relative;
  cursor: pointer;
  display: inline-block;
  text-decoration: none;
  outline: none;
}

.tax span {
  visibility: hidden;
  color: black;
  position: absolute;
  text-align: justify;
  top: 30px;
  left: 50%;
  z-index: 999;
  width: 400px;
  margin-left: -300px;
  padding: 10px;
  border: 1px solid #ccc;
  opacity: .9;
  background-color: white;
  background-image: linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0));
  border-radius: 4px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.4), 0 1px 0 rgba(255, 255, 255, 0.5) inset;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.62);
}

.tax:hover {
  border: 0;
  /* IE6 fix */
}

.tax:hover span {
  visibility: visible;
}

.tax span:after {
  content: "";
  position: absolute;
  z-index: 1000;
  top: -8px;
  left: 75%;
  margin-left: -8px;
  border-bottom: 8px solid #ddd;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 0;
}

.tax span:before {
  border-top-color: #ccc;
  bottom: -8px;
}

