section {
  padding-left: 80px;
  padding-right: 80px;
}

.border {
  border-left: 1px solid #ebebeb;
  border-right: 1px solid #ebebeb;
  border-bottom: 1px solid #ebebeb;
}

.dashboard-container {
  background-color: #f7f7f7;
}

.dashboard-container label {
  width: 100%;
  background-color: #fff;
  margin-bottom: 0px;
  font-size: 26px;
}

.dashboard-container .nav-tabs {
  display: -ms-flexbox;
  display: flex;
  background-color: #fff;
}

.dashboard-container .nav-tabs li {
  -ms-flex: 1;
      flex: 1;
  text-align: center;
  margin-right: 0;
  border-radius: 0;
  border: 1px solid #ebebeb;
  font-size: 19.44px;
}

.dashboard-container .nav-tabs li a,
.dashboard-container .nav-tabs li a:hover {
  border: none;
  background: none;
  color: #000;
  padding: 17px 15px;
}

.dashboard-container .nav-tabs li:not(:first-child) {
  margin-left: -1px;
}

.dashboard-container .nav-tabs li.active {
  border-top: 8px solid #bb0076;
  background-color: #f7f7f7;
}

.dashboard-container .nav-tabs li.active a {
  color: #bb0076;
}

.dashboard-container .tab-content .active {
  padding-left: 0px;
}

.dashboard-container .row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: start;
      align-items: flex-start;
}

.dashboard-container .row .col-sm {
  padding: 10px;
  text-align: center;
}

.padding-10 {
  padding: 10px;
}

.padding-20 {
  padding: 20px;
}

.padding-30 {
  padding: 30px;
}

.clickable {
  cursor: pointer;
}

.col {
  float: left;
}

.cols-1 {
  width: 0.833%;
}

.cols-2 {
  width: 16.66%;
}

.cols-3 {
  width: 25%;
}

.cols-4 {
  width: 33.33%;
}

.cols-5 {
  width: 41.66%;
}

.cols-6 {
  width: 50%;
}

.cols-7 {
  width: 58.33%;
}

.cols-8 {
  width: 66.66%;
}

.cols-9 {
  width: 75%;
}

.cols-10 {
  width: 83.33%;
}

.cols-11 {
  width: 91.66%;
}

.cols-12 {
  width: 100%;
}

.graph {
  text-align: center;
}

.graph .clickable {
  height: 320px;
  padding: 10px 25px;
}

.graph .sub-body {
  height: 500px;
}

.graph .module-title {
  text-align: left;
}

.graph h4 {
  margin-bottom: 0;
}

.graph span {
  font-size: 10px;
}

.content-details {
  position: fixed;
  top: 50px;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 1500;
  background-color: rgba(0, 0, 0, 0.5);
  text-align: center;
}

.content-details .content-details-dialog {
  position: absolute;
  top: 50px;
  left: 0;
  right: 0;
  margin: auto;
  background-color: #fff;
  font-family: "Open Sans";
  font-size: 16px;
  text-align: left;
  max-width: 1200px;
  border: 1px solid #efefef;
}

.content-details .content-details-dialog .content-details-dialog-header {
  width: 100%;
  height: 50px;
  padding: 10px;
  margin-bottom: 10px;
}

.content-details .content-details-dialog .content-details-dialog-body {
  width: 100%;
  padding: 10px;
}

.content-details .content-details-dialog .sub-body {
  padding: 0 20px;
}

.content-details .content-details-dialog .sub-body .btn-no-back {
  width: 100%;
  font-size: 14px;
}

.content-details .content-details-dialog .chart {
  width: 100%;
  transform: scale(1);
}

.clear {
  clear: both;
}

.dialog-footer {
  position: absolute;
  bottom: 20px;
  right: 20px;
  height: 40px;
  width: 160px;
  background: url("../../../assets/img/logo.png") center left no-repeat #000;
  background-size: contain;
  font-family: Open Sans;
  font-size: 14px;
  color: #fff;
}

.sub-body {
  padding: 0 20px;
}

.button-close {
  width: 20px;
  height: 20px;
